body {
  margin: 0;
  padding: 0;
  background:
    -webkit-gradient(linear, left bottom, left top, from(rgba(0,0,0,.6)), color-stop(transparent), to(rgba(0,0,0,.6))),
    url("img/Pattern.png");
  background:
    -webkit-linear-gradient(bottom, rgba(0,0,0,.6), transparent, rgba(0,0,0,.6)),
    url("img/Pattern.png");
  background:
    -o-linear-gradient(bottom, rgba(0,0,0,.6), transparent, rgba(0,0,0,.6)),
    url("img/Pattern.png");
  background:
    linear-gradient(0deg, rgba(0,0,0,.6), transparent, rgba(0,0,0,.6)),
    url("img/Pattern.png");
  background-repeat: repeat;
  font-family: 'Exo 2', sans-serif;
}

h1 {
  font-weight: normal;
}



